.footer {
  position: relative;
  margin-top: 50px;
  padding: 0;

  @include contentpadding;

  &__images {
    display: flex;
    justify-content: space-between;

    @include ondesktop {
      margin: 48px 0 0 55px;
    }
  }

  &__logo {
    margin-left: 20px;
    width: 63px;
    height: 72px;
    transition: transform 0.5s ease-in-out;

    &:hover {
      transform: scale(1.03);
    }


    @include ontable {
      margin-left: 34px;
      width: 98px;
    }

    @include ondesktop {
      margin-left: 0;
      width: 63px;
    }
  }

  &__icons-wrapper {
    margin-right: 20px;

    @include ontable {
      position: absolute;
      margin: 112px 0 0 34px;
    }

    @include ondesktop {
      margin: 134px 0 0;

    }
  }

  &__icons {
    display: inline-block;
    background-size: cover;
    margin-top: 24px;
    width: 24px;
    height: 24px;

    @include ontable {
      margin-top: 0;
    }
  }

  &__facebook {
    cursor: pointer;
    background-image: url(../images/facebook-letter-logo.svg);
    margin-right: 20px;
    transition: transform 0.5s ease-in-out;

    &:hover {
      transform: scale(1.03);
    }

    @include ontable {
      margin-right: 32px;
    }
  }

  &__instagram {
    cursor: pointer;
    background-image: url(../images/instagram-logo.svg);
    transition: transform 0.5s ease-in-out;

    &:hover {
      transform: scale(1.03);
    }
  }
}
