.subscribe {
  margin: 0;
  margin-top: 579px;

  @include ontable {
    margin-top: 724px;
  }

  @include ondesktop {
    margin-top: 1246px;
    height: 550px;
  }

  &__background {
    background-image: url(../images/sub__image.png);
    background-position: center;
    background-size: cover;
    width: 100%;
    height: 386px;

    @include ontable {
      // margin-top: 724px;
      height: 380px;
    }

    @include ondesktop {
      height: 550px;
    }
  }

  &__content {
    @include contentpadding;

    position: absolute;
    left: 50%;
    transform: translate(-50%, 0);
    padding-top: 88px;

    @include ontable {
      padding-top: 82px;
    }

    @include ondesktop {
      padding-top: 163px;
    }
  }

  &__title {
    margin: 0;
    font-family: Montserrat, sans-serif;
    font-weight: 700;
    font-size: 36px;
    text-align: center;
    color: $main-white-color;

    @include ontable {
      font-size: 48px;
    }
  }

  &__text {
    margin: 0;
    padding-top: 20px;
    font-family: 'IBM Plex Sans', sans-serif;
    font-weight: 500;
    font-size: 18px;
    text-align: center;
    color: $main-white-color;

    @include ontable {
      padding-top: 16px;
    }
  }

  &__email {
    margin: 248px 0 0;
    position: absolute;
    outline: none;
    border: none;
    width: 280px;
    left: 50%;

    transform: translate(-50%, 0);

    @include ontable {
      width: 460px;
      margin: 228px 0 0;
    }

    @include ondesktop {
      width: 570px;
      margin: 317px 0 0;
    }

    &__form {
      display: flex;
      box-sizing: border-box;
      outline: none;
      border: 0;
      width: 230px;
      height: 50px;
      padding: 16px 24px;
      background-color: $main-white-color;

      &:autofill {
        background-color: $main-white-color;
      }

      @include ontable {
        width: 374px;
      }

      @include ondesktop {
        width: 470px;
      }

      &__email {
        box-sizing: border-box;
        width: 100% ;
        outline: none;
        border: none;
        background-color: $main-white-color;
        appearance: none;

        &:focus {
          -webkit-box-shadow: 0 0 0 1000px $main-white-color inset !important;
        }
      }
    }

    &__button {
      cursor: pointer;
      position: absolute;
      width: 50px;
      height: 50px;
      background-color: $color-for-button;
      bottom: 0;
      right: 0;
      transition: 0.5s ease-in-out;

      &:hover {
        opacity: 80%;
      }

      &__vector {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
    }
  }
}
